.main {
  width: 100%;
  overflow: hidden;
}

.main-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @media (max-width: 767px) {
    flex-direction: column;
  }
}

.header-wrapper {
  padding: 32px 0;

  @media (max-width: 767px) {
    padding: 16px 0;
  }
}
